.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  position: fixed;
  top: 0;
}

.drawer{
  z-index: 2;
  position: relative;
  cursor: pointer;
  font-size: 30px;
  top: 0;
  color: white;
  margin: 10px;
}
.drawer .m-drawer{
  display: none;
}
.drawer .l-drawer{
  display: block;
}
@media only screen and (max-width: 700px) {
  .App {
    display: block;
    position: absolute;
  }
  .drawer .m-drawer{
    display: block;
  }
  .drawer .l-drawer{
    display: none;
  }
}