.container{
    margin-left: 7%;
    margin-right: 7%;
}

a{
    text-decoration: none;
    color: inherit;
}
.page{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    width: 80vw;
    position: relative;
    transition: 0.85s;
}
.page.full{
    width: 100vw;
    transition: 0.85s;
}
.home{
    text-align: center;
}
.intro{
    font-family: Nunito,sans-serif;
    font-size: 50px;
    font-weight: 400;
    transition: all 0.8s;
}
.intro span{
    color: #434eb3;
}
.page-p{
    display: inline-block;
    text-align: center;
    width: 50%;
    transition: all 2.8s;
}
@media only screen and (max-width: 1000px) {
    .intro{
        font-size: 35px;
    }
    .page-p{
        width: 70%;
        font-weight: 100;
    }
    .page{
        width: 70vw;
    }
}
@media only screen and (max-width: 700px) {
    .intro{
        font-size: 35px;
    }
    .page-p{
        width: 80%;
        font-weight: 100;
    }
    .page{
        width: 100vw;
    }
    .page.full{
        position: absolute;
        transition: 0.8s;
    }
}
@media only screen and (max-width: 500px) {
    .intro{
        font-size: 30px;
        padding: 5px;
    }
    .page-p{
        width: 80%;
        font-weight: 100;
        font-size: 14px;
    }
    .page{
        width: 100vw;
        overflow-y: auto;
    }
    .page.full{
        position: absolute;
        transition: 0.8s;
    }
}
@media only screen and (max-width: 300px) {
    .intro{
        font-size: 18px;
    }
    .page-p{
        width: 80%;
        font-weight: 100;
        font-size: 10px;
    }
    .page{
        width: 100vw;
        overflow-y: auto;
    }
    .page.full{
        position: absolute;
        transition: 0.8s;
    }
}