.side-nav{
    display: flex;
    flex-direction: column;
    width: 0;
    position: relative;
    transition: all 0.8s;
    opacity: 0;
    color: white;
}
.side-nav.active{
    width: 20vw;
    transition: all 0.8s;
    opacity: 1;
}
.side-align{
    align-content: center;
}
.side-nav-end{
    justify-content: flex-end;
    width: 100%;
    height: 100%;
}
.side-nav-top{
    justify-content: flex-start;
    width: 100%;
    height: 100%;
}
.side-nav-center{
    justify-content: center;
    width: 100%;
    height: 100%;
}
.nav-list{
    display: flex;
    flex-direction: column;
}
.nav-image{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.img-circle{
    border-style: solid;
    border-radius: 50%;
    border-width: 1px;
    border-color:  rgba(255,255,255,0.1);
    width: 200px;
    height: 200px;
    margin-top: 25px;
}
.footer{
    border-top-style: solid;
    border-top-color:  rgba(255,255,255,0.1);
    border-width: 1px;
    padding: 15px;
}
.active.sbg-dark{
    background-color: #282c49;
    border-right-style: solid;
    border-right-color:  rgba(255,255,255,0.1);
    border-width: 1px;
    color: white;
}
.sbg-dark{
    background-color: #282c49;
    border-right-style: none;
}

@media only screen and (max-width: 1000px) {
    .side-nav.active{
        width: 30vw;
        height: 100vh;
        overflow-y: auto;
    }
}
@media only screen and (max-width: 700px) {
    .side-nav.active{
        opacity: 0;
        width: 0;
        height: 0;
        transition: height 0.8s;
    }
    .side-nav{
        opacity: 1;
        width: 100vw;
        height: 85vh;
        transition: height 0.8s;
        overflow-y: auto;
    }
    .sbg-dark{
        border-bottom-style: solid;
        border-bottom-color:  rgba(255,255,255,0.1);
        border-width: 1px;
    }
    .sbg-dark.active{
        border-bottom-style: none;
    }
}